<template>
  <div name="modal-confirm" class="modal-save-sucess">
    <modal-std :id="id"
               size="small"
               :btnCloseDisp="false"
               @close="$emit('close')"
               ref="modalConfirm">
      <template #header>
        <h2>
          <span class="slds-card__header-link slds-text-heading_small">Confirmation</span>
        </h2>
      </template>
      <template #content>
        <div class="custom-position">
            <span>{{ 'Are you sure you want to ' + title }}?</span>
        </div>
      </template>
      <template #footer>
          <button class="slds-button slds-button_neutral" @click="onClose">No</button>
          <button class="slds-button slds-button_success" @click="onOk">Yes</button>
      </template>
    </modal-std>
  </div>
</template>
<script>
export default {
  name: 'modal-confirm',
  props: ['id', 'modalConfirmOk', 'title'],
  methods: {
    onOpen () {
      // console.log('Confirm Open')
      this.$refs.modalConfirm.onOpen()
    },
    onClose () {
      this.$refs.modalConfirm.onClose()
    },
    onOk () {
      this.$refs.modalConfirm.onClose()
      this.modalConfirmOk()
    }
  }
}
</script>

<style scoped>
  .icon-success-block {
    text-align: right;
  }
  .icon-success {
    padding-right: 25px;
    font-size: 72px;
    color: #4aa54b;
    vertical-align: middle;
  }
  .message-success {
    font-size: 36px;
  }
  .message-success-extend {
    font-size: 18px;
    opacity: 0.5;
  }
  .custom-position {
    text-align: center;
    margin: 5px 0px 5px 0px;
  }

</style>



