<template>
  <div class="create-notifaction">
    <!-- emoc == {{emoc}} -->
    <modal-confirm id="discard" ref="modalConfirm" :modalConfirmOk="modalConfirmOk" title="discard"/>
    <modal-confirm id="cancel" ref="modalConfirmCancel" :modalConfirmOk="modalConfirmCancelOk" title="cancel"/>
    <modal-std id="model-loder"
               size="small"
               :btnCloseDisp="false"
               @close="$emit('close')"
               ref="modalLoader">
      <template #content>
        <div class="custom-modal-loader">
          <img :src="require('../assets/iconStd/loader.gif')" style="width: 80px"/>
          <p style="margin-top: 20px;"><span class="slds-card__header-link slds-text-heading_small">Please wait...</span></p>
        </div>
      </template>
    </modal-std>
    <modal-std :id="modalId"
               size="large"
               @close="$emit('close')"
               :btnCloseDisp="false"
               ref="modalNotification">
      <template #header>
        <div class="slds-media__body custom-h2-noti" >
          <h2>
            <span class="slds-card__header-link slds-text-heading_small">New Notification</span>
          </h2>
        </div>
      <!-- <div class="slds-card__header slds-grid header custom-border-header">
        <header class="slds-media slds-media_center slds-has-flexi-truncate">
          <div class="slds-media__body custom-h2-noti" >
            <h2>
              <span class="slds-card__header-link slds-text-heading_small">New Notification</span>
            </h2>
          </div>
        </header>
      </div> -->
      </template>
      <template #content>
          <moc-notification :emoc="emoc" ref="MocNotification"/>
      </template>
      <!-- <modal-after-save @close="closePopupSuccess"
                          ref="popupAfterSave"/> -->
      
      <template #footer>
          <div class="row">
            <div class="col-md-12 custom-align">
              <button class="slds-button slds-button_neutral" @click="closePopup">Cancel</button>
              <button class="slds-button slds-button_brand" @click="clickSave">Submit</button>
            </div>
          </div>
      </template>
      <!-- <footer class="slds-card__footer">
        <div class="row">
          <div class="col-md-4"></div>
          <div class="col-md-4">
            <button class="slds-button slds-button_brand" @click="clickSave">Submit</button>
          </div>
          <div class="col-md-4"></div>
        </div>
      </footer> -->
    </modal-std>
    <div v-if="!emoc.Notification__c" class="custom-button-create">
    <!-- <div v-if="false" class="custom-button-create"> -->
      <button class="slds-button slds-button_success" @click="showPopup" :disabled="this.loading">Create Notification</button>
    </div>
    <div v-else style="margin: 20px">
      <div class="row">
        <div class="col-md-12 custom-align-end">
          <select-action
            v-model="selectedAc"
            name="selected"
            :options="actionList"
            :handleAction="handleAction"
          ></select-action>
        </div>
      </div>
      <moc-notification-detail :emoc="emoc" :selectedAc="selectedAc" ref="MocNotificationDetail"/>
       <div v-show="selectedAc === 'Edit'" class="row">
        <div class="col-md-12 custom-align">
          <button class="slds-button slds-button_neutral" @click="showPopupCancel">Cancel</button>
          <button class="slds-button slds-button_brand" @click="clickUpdate">Update</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import SelectAction from '../standardSalesforceCmp/SelectAction.vue'
import MocNotification from './MocNotification.vue'
import MocNotificationDetail from './MocNotificationDetail.vue'
import ModalConfirm from '../standardSalesforceCmp/ModalConfirm.vue'

export default {
  name: 'create-notifaction',
  data () {
    return {
      numpage: 3,
      hasError: false,
      errorBagItems: [],
      recId: '',
      resultSuccess: {},
      cpmCapexNextFalse: false,
      modalId: 'modalNotification',
      selectedAc: '',
      actionList: [],
      isOpen: false,
      loading: true
    }
  },
  watch: {
    'selectedAc' (newVal) {
      if (newVal) {
        this.handleAction(newVal)
      }
    },
    'emoc.Notification__c' (newVal) {
      if (newVal) {
        let paramSearch = {
          id: newVal
        }
        this.$store.dispatch('receiveNotification', {sendParam: paramSearch, afterSet: () => {}})
      }
    },
    'emoc.Integration_Status__c' () {
      this.onSetDataOption()
    }
  },
  created () {
    this.$store.dispatch('receiveLookupFncLocation', '')
    this.$store.dispatch('receiveLookupUser', { type: 'main', sendParam: '' })
    this.onSetDataOption()
  },
  mounted () {
    const queryString = window.location.search
    const urlParams = new URLSearchParams(queryString)
    this.recId = urlParams.get('id')
    let paramSearch = {
      emocApplicationId: this.recId
    }
    if (this.recId) {
      this.$store.dispatch('receiveEmocApplication', paramSearch).then(() => {
        this.loading = false
      })
    }
  },
  computed: {
    emoc () { return this.$store.state.emoc }
  },
  methods: {
    onOpen () {
      this.isOpen = !this.isOpen
    },
    closePopup () {
      this.$refs.modalNotification.onClose()
    },
    showPopupCancel () {
      this.$refs.modalConfirmCancel.onOpen()
    },
    modalConfirmCancelOk () {
      this.showPopupLoader()
      setTimeout(() => {
        this.closePopupLoader()
        this.selectedAc = ''
      }, 500)
    },
    modalConfirmOk () {
      let apiName = 'deleteNotification'
      let obj = { Id: this.emoc.Notification__c, E_MOC_Application_No__c: this.recId }
      this.handlePutData(apiName, obj)
    },
    onSetDataOption () {
      let dataEmoc = this.emoc
      this.actionList = [
        {
          value: 'Edit',
          label: 'Edit',
          disabled: !(dataEmoc.Integration_Status__c !== 'Success')
        },
        {
          value: 'Close',
          label: 'Close',
          disabled: true // !(dataEmoc.Integration_Status__c === 'Success')
        },
        {
          value: 'Discard',
          label: 'Discard',
          disabled: !(dataEmoc.Integration_Status__c !== 'Success')
        },
        {
          value: 'Resend',
          label: 'Resend',
          disabled: !(dataEmoc.Integration_Status__c !== 'Success')
        }
      ]
      // console.log('actionList: ', this.actionList)
    },
    showPopup () {
      this.$refs.modalNotification.onOpen()
    },
    showPopupLoader () {
      this.$refs.modalLoader.onOpen()
    },
    closePopupLoader () {
      this.$refs.modalLoader.onClose()
    },
    clickUpdate () {
      // console.log('Id: ', this.emoc.Notification__c)
      // console.log('Requester__c', this.emoc.Requestor_ID__c)
      let obj = { Id: this.emoc.Notification__c,
        Requester__c: this.emoc.Requestor_ID__c,
        Department_Resp__c: this.emoc.Department_Resp__c,
        Indicator__c: this.emoc.Indicator__c,
        Requestor_ID__c: this.emoc.Requestor_ID__c,
        Requester_PISUser__c: this.emoc.Requester_PISUser__c
      }
      this.handlePutData('putNotification', obj)
    },
    clickSave () {
      // console.log('submit notification')
      Promise.all([
        this.$refs.MocNotification.$validator.validateAll()
      ]).then((result) => {
        if (result.filter(df => !df).length === 0) {
          this.closePopup()
          this.showPopupLoader()
          let renewParam = this.recId // JSON.stringify(paramSave);
          // console.log('renewParam: ', renewParam)
          this.$store.dispatch('submitNotification', renewParam)
            .then(result => {
              if (result.statusSuccess) {
                this.resultSuccess = result
                // this.showPopupLoader()
                this.getNotificationData(result)
                // this.linkToDetail()
              }
            })
          this.hasError = false
        } else {
          this.errorBagItems = this.errors.items
          this.hasError = true
        }
      })
    },
    getNotificationData (result, apiName = 'receiveNotification', refresh) {
      let obj = { id: result.Id }
      if (apiName === 'receiveEmocApplication') {
        obj = { ...obj, emocApplicationId: this.recId }
        this.emoc.Notification__c = null
        setTimeout(() => {
          this.closePopupLoader()
        }, 3000)
        return
      }
      this.$store.dispatch(apiName, { sendParam: obj, afterSet: this.afterGetNoti })
    },
    afterGetNoti () {
      this.closePopupLoader()
      this.$refs.MocNotificationDetail && this.$refs.MocNotificationDetail.setFlagAlert(false)
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    handleAction (action) {
      // console.log('handleAction: ', action)
      let apiName = 'putNotification'
      let obj = {}
      if (action === 'Close') {
        obj = { Id: this.emoc.Notification__c, Notification_Status__c: 'Closed', Integration_Status__c: 'In Progress', Integration_Message__c: '' }
      } else if (action === 'Discard') {
        this.$refs.modalConfirm.onOpen()
        return
      } else if (action === 'Edit') {
        this.showPopupLoader()
        setTimeout(() => {
          this.closePopupLoader()
        }, 500)
        return
      } else if (action === 'Resend') {
        obj = { Id: this.emoc.Notification__c, Integration_Status__c: 'In Progress', Integration_Message__c: '' }
      }
      this.handlePutData(apiName, obj)
    },
    handlePutData (apiName, obj) {
      console.log(apiName, obj)
      this.showPopupLoader()
      this.$store.dispatch(apiName, obj).then((result) => {
        if (result.statusSuccess) {
          let fetchWithId = { Id: this.emoc.Notification__c }
          if (apiName === 'deleteNotification') {
            // fetchWithId = { Id: this.recId }
            this.getNotificationData(fetchWithId, 'receiveEmocApplication')
          } else {
            this.getNotificationData(fetchWithId)
          }
        }
      })
    }
  },
  components: {
    MocNotification,
    SelectAction,
    MocNotificationDetail,
    ModalConfirm
  }
}
</script>

<style scoped>
  .custom-button-create {
    text-align: end;
    margin: 10px;
  }
  .custom-align {
    text-align: center;
  }
  .custom-align-end {
    display: flex;
    justify-content: flex-end;
  }
  .custom-modal-loader {
    text-align: center;
    margin: 20;
  }
</style>

<style src="../assets/style.css"></style>


