<template>
  <div name="moc-notification">
      <div class="data-frame">
        <div v-if="this.emoc.Integration_Status__c !== 'Success' && this.emoc.Integration_Status__c !== '' && this.emoc.Notification_Status__c !== 'Closed' && !flagAlert" class="row">
          <div class="col-sm-12" style="background-color: red; text-align: center; color: white; font-size: 13px; position: relative">
              <span>
                <i class="fa fa-ban" aria-hidden="true" ></i>
              </span>
              <span>
                &nbsp; {{emoc.Integration_Message__c || 'Integration Failed: Your network could not connect to Server. Please contact System Administer.'}}
              </span>
              <span style="position: absolute; right: 10px;">
                <button @click="setFlagAlert(!flagAlert)">
                  X
                </button>
              </span>
          </div>
        </div>
         <div class="row">
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Name" label="Notification" disabled></input-text-std>
          </div>
          <div v-if="emoc.Notification_Type__c === 'CR'" class="col-sm-6"></div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Notification_Type__c" label="Notification Type" disabled></input-text-std>
          </div>
          <div v-if="emoc.Notification_Type__c === 'CR'" class="col-sm-6">
            <input-text-std v-model="emoc.Order__c" label="Order" disabled></input-text-std>
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Notification_Status__c" label="Notification Status" disabled></input-text-std>
          </div>
      <!--     <div class="col-sm-6">
            <input-text-std v-model="emoc.Notification_Closed_date__c" label="Notification Close date " disabled></input-text-std>
          </div> -->
          <div class="col-sm-6">
            <label class="slds-form-element__label">Notification Close date</label>
            <v-menu v-model="showPickerEndDate"
                    :close-on-content-click="false"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="emoc.Notification_Closed_date__c"
                  name="Notification_Closed_date__c"
                  @blur="dateEnd = parseDate(emoc.Notification_Closed_date__c)"
                  placeholder="dd/mm/yyyy"
                  v-bind="attrs"
                  v-on="on"
                  append-icon="mdi-calendar"
                  autocomplete="off"
                  hide-details
                  outlined
                  dense
                  disabled
                ></v-text-field>
              </template>
              <v-date-picker
                no-title
                v-model="dateEnd"
                @change="showPickerEndDate = false"
                :min="minCalendar"
              ></v-date-picker>
            </v-menu>
            <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Notification_Closed_date__c') }}</span></div>
          </div>

          <div v-if="emoc.Notification_Type__c === 'CR'" class="col-sm-6">
            <input-text-std v-model="emoc.User_Status__c" label="User Status" disabled></input-text-std>
          </div>
        </div>
        <div v-if="emoc.Notification_Type__c === 'CR'" class="row">
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Priority__c" label="Priority" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <lookups-std v-model="emoc.Functional_Location__c"
                          v-validate="'required'"
                          data-vv-as="Functional Location"
                          data-vv-value-path="inputValue"
                          label="Functional Location"
                          placeholder="Search..."
                          :options="optionsLookupFunctionalLocation"
                          :error="errors.has('Functional_Location__c')"
                          name="Functional_Location__c"
                          @search="filterLookupFunctionalLocation(arguments[0])"
                          icon="account"
                          disabled
                          loading>
                <template #error>
                  <span>{{ errors.first('Functional_Location__c') ? 'Complete this field.' : '' }}</span>
                </template>
            </lookups-std>
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Main_Work_Center_Text__c" label="Main Work Center" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">

            <lookups-std v-model="emoc.Requestor_Name__c"
                         data-vv-value-path="inputValue"
                         label="Requester"
                         placeholder="Search..."
                         :options="optionsLookupUser()"
                         :error="errors.has('Requestor_Name__c')"
                         name="Requestor_Name__c"
                         @search="filterLookupUser(arguments[0])"
                         @setDataRow="setDataRow(arguments[0])"
                         icon="people"
                         loading
                         :disabled="!(selectedAc === 'Edit')">
            </lookups-std>
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Indicator__c" label="Indicator" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Employee_Id__c" label="Requestor ID" disabled></input-text-std>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
import moment from 'moment'

export default {
  data () {
    return {
      flagAlert: false,
      lookupType: 'detail',
      showPickerEndDate: false,
      dateEnd: '',
      minCalendar: moment().format('YYYY-MM-DD')
    }
  },
  name: 'moc-notification',
  props: [
    'emoc', 'selectedAc'
  ],
  created () {
    if (this.emoc.Requestor_Name__c !== '' && this.emoc.Requestor_Name__c) {
      this.filterLookupUser(this.emoc.Requestor_Name__c)
    }
    if (this.emoc.Functional_Location__c !== '' && this.emoc.Functional_Location__c) {
      this.filterLookupFunctionalLocation(this.emoc.Functional_Location__c)
    }
  },
  watch: {
    // 'emoc.Requestor_Name__c' (newVal) {
    //   console.log('this.lookupUser', this.lookupUser)
    //   if (newVal !== '' && newVal) {
    //     // this.filterLookupFunctionalLocation(newVal)
    //     this.filterLookupUser(newVal)
    //     let defaultFomula = this.lookupUser.filter(df => {
    //       return df.Id === newVal
    //     })
    //     if (defaultFomula.length !== 0) {
    //       this.emoc.Employee_Id__c = defaultFomula[0].Employee_Id__c || this.emoc.Employee_Id__c
    //       this.emoc.Requestor_ID__c = defaultFomula[0].Id || this.emoc.Requestor_ID__c
    //       this.emoc.Department_Resp__c = defaultFomula[0].ORGID__c || this.emoc.Department_Resp__c
    //       this.emoc.Indicator__c = defaultFomula[0].Indicator__c || this.emoc.Indicator__c
    //       this.emoc.Requester_PISUser__c = defaultFomula[0].PISUsers__c || this.emoc.Requester_PISUser__c
    //     }
    //   } else {
    //     this.emoc.Employee_Id__c = ''
    //     this.emoc.Requestor_ID__c = ''
    //     this.emoc.Department_Resp__c = ''
    //     this.emoc.Indicator__c = ''
    //     this.emoc.Requester_PISUser__c = ''
    //     this.filterLookupUser('')
    //   }
    // },
    'emoc.Functional_Location__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupFunctionalLocation(newVal)
      }
    },
    'emoc.Requestor_Name__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    },
    dateEnd (val) {
      this.emoc.Notification_Closed_date__c = this.formatDate(this.dateEnd)
    }
  },
  computed: {
    lookupFncLocation () { return this.$store.state.lookupFncLocation },
    lookupUser () { return this.$store.state.lookupUser },
    optionsLookupFunctionalLocation () {
      return this.lookupFncLocation.map(dm => {
        return {
          value: dm.Id,
          label: dm.Name,
          icon: 'account'
        }
      })
    }
    // optionsLookupUser () {
    //   return this.lookupUser.map(dm => {
    //     return {
    //       value: dm.Id,
    //       label: dm.FirstName + ' ' + dm.LastName,
    //       icon: 'people'
    //     }
    //   })
    // }
  },
  methods: {
    setFlagAlert (flag) {
      this.flagAlert = flag
    },
    filterLookupFunctionalLocation (searchParam) {
      this.$store.dispatch('receiveLookupFncLocation', searchParam)
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', {type: this.lookupType, sendParam: searchParam})
    },
    optionsLookupUser () {
      if (typeof this.lookupUser[this.lookupType] !== 'undefined') {
        return this.lookupUser[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.FirstName + ' ' + dm.LastName,
            icon: 'people',
            Employee_Id__c: dm.Employee_Id__c,
            ORGID__c: dm.ORGID__c,
            Indicator__c: dm.Indicator__c,
            PISUsers__c: dm.PISUsers__c
          }
        })
      } else {
        return []
      }
    },
    setDataRow (dataRow) {
      this.emoc.Employee_Id__c = dataRow.Employee_Id__c
      this.emoc.Requestor_ID__c = dataRow.Id || this.emoc.Requestor_ID__c
      this.emoc.Department_Resp__c = dataRow.ORGID__c || this.emoc.Department_Resp__c
      this.emoc.Indicator__c = dataRow.Indicator__c
      this.emoc.Requester_PISUser__c = dataRow.PISUsers__c || this.emoc.Requester_PISUser__c
      if (typeof dataRow.Requestor_Name__c === 'undefined') {
        this.filterLookupUser('')
      }
    },
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    }
  }
}
</script>


