<template>
  <div name="moc-notification">
      <div class="data-frame">
        <div class="row">
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Notification_Type__c" label="Notification Type" disabled></input-text-std>
             <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Notification_Type__c') ? 'Complete this field.' : '' }}</span></div>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <lookups-std v-model="emoc.Functional_Location__c"
                          v-validate="'required'"
                          data-vv-as="Functional Location"
                          data-vv-value-path="inputValue"
                          label="Functional Location"
                          placeholder="Search..."
                          :options="optionsLookupFunctionalLocation"
                          :error="errors.has('Functional_Location__c')"
                          name="Functional_Location__c"
                          @search="filterLookupFunctionalLocation(arguments[0])"
                          icon="account"
                          disabled
                          loading>
                <template #error>
                  <span>{{ errors.first('Functional_Location__c') ? 'Complete this field.' : '' }}</span>
                </template>
            </lookups-std>
          </div>
          <div class="col-sm-6">
              <input-text-std
                v-model="emoc.Functional_Location_Description__c"
                v-validate="'max:40'"
                data-vv-value-path="inputValue"
                name="Functional_Location_Description__c"
                label="Functional Location Description"
                :error="errors.has('Functional_Location_Description__c')"
                :maxlength="40"
                disabled
              >
              </input-text-std>
            </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Plant_of_Main_Work_Center__c" label="Plant of Main Work Center" disabled></input-text-std>
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Main_Work_Center__c" label="Main Work Center" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <lookups-std v-model="emoc.Requestor_Name__c"
                         data-vv-value-path="inputValue"
                         label="Requester"
                         placeholder="Search..."
                         :options="optionsLookupUser()"
                         :error="errors.has('Requestor_Name__c')"
                         name="Requestor_Name__c"
                         @search="filterLookupUser(arguments[0])"
                         @setDataRow="setDataRow(arguments[0])"
                         icon="people"
                         loading
                         disabled>
            </lookups-std>
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Indicator__c" label="Indicator" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Employee_Id__c" label="Requestor ID" disabled></input-text-std>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
export default {
  name: 'moc-notification',
  props: [
    'emoc'
  ],
  data () {
    return {
      lookupType: 'detail'
    }
  },
  created () {
    if (this.emoc.Requestor_Name__c !== '' && this.emoc.Requestor_Name__c) {
      this.filterLookupUser(this.emoc.Requestor_Name__c)
    }
    if (this.emoc.Functional_Location__c !== '' && this.emoc.Functional_Location__c) {
      this.filterLookupFunctionalLocation(this.emoc.Functional_Location__c)
    }
  },
  watch: {
    'emoc.Requestor_Name__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    },
    'emoc.Functional_Location__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupFunctionalLocation(newVal)
      }
    }
  },
  computed: {
    lookupFncLocation () { return this.$store.state.lookupFncLocation },
    lookupUser () { return this.$store.state.lookupUser },
    optionsLookupFunctionalLocation () {
      return this.lookupFncLocation.map(dm => {
        return {
          value: dm.Id,
          label: dm.Name,
          icon: 'account'
        }
      })
    }
    // optionsLookupUser () {
    //   return this.lookupUser.map(dm => {
    //     return {
    //       value: dm.Id,
    //       label: dm.FirstName + ' ' + dm.LastName,
    //       icon: 'people'
    //     }
    //   })
    // }
  },
  methods: {
    filterLookupFunctionalLocation (searchParam) {
      this.$store.dispatch('receiveLookupFncLocation', searchParam)
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', {type: this.lookupType, sendParam: searchParam})
    },
    optionsLookupUser () {
      if (typeof this.lookupUser[this.lookupType] !== 'undefined') {
        return this.lookupUser[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.FirstName + ' ' + dm.LastName,
            icon: 'people',
            Employee_Id__c: dm.Employee_Id__c,
            ORGID__c: dm.ORGID__c,
            Indicator__c: dm.Indicator__c,
            PISUsers__c: dm.PISUsers__c
          }
        })
      } else {
        return []
      }
    },
    setDataRow (dataRow) {
      this.emoc.Employee_Id__c = dataRow.Employee_Id__c || this.emoc.Employee_Id__c
      this.emoc.Requestor_ID__c = dataRow.Id || this.emoc.Requestor_ID__c
      this.emoc.Department_Resp__c = dataRow.ORGID__c || this.emoc.Department_Resp__c
      this.emoc.Indicator__c = dataRow.Indicator__c || this.emoc.Indicator__c
      this.emoc.Requester_PISUser__c = dataRow.PISUsers__c || this.emoc.Requester_PISUser__c
      if (typeof dataRow.Functional_Location__c === 'undefined') {
        this.filterLookupUser('')
      }
    }
  }
}
</script>


