import Vue from 'vue'
import Vuex from 'vuex'
import createNotificationApi from '../api/createNotification'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    lookupFncLocation: [],
    lookupUser: {},
    emoc: {
      // Id: null,
      E_MOC_Application_No__c: '',
      Notification_Type__c: 'CR',
      Functional_Location__c: '',
      Functional_Location_Description__c: '',
      Plant_of_Main_Work_Center__c: '',
      Main_Work_Center__c: '',
      Requestor_ID__c: '',
      Requestor_Name__c: '',
      Description__c: '',
      Department_Resp__c: '',
      Indicator__c: '',
      Notification__c: '',
      Integration_Status__c: '',
      Integration_Message__c: '',
      Order__c: '',
      Notification_Status__c: '',
      User_Status__c: '',
      Priority__c: '',
      Main_Work_Center_Text__c: '',
      Requester__c: '',
      Name: '',
      Requester_PISUser__c: '',
      Employee_Id__c: '',
      Notification_Closed_date__c: ''
    },
    roundOfTimeout: 0
  },
  getters: {
    getLookupFncLocation: (state) => {
      return state.lookupFncLocation
    },
    getLookupUser: (state) => {
      return state.lookupUser
    }
  },
  mutations: {
    setLookupFncLocation: (state, data) => {
      state.lookupFncLocation = data
    },
    setLookupUser: (state, {type, data}) => {
      let temp = state.lookupUser
      temp[type] = data
      state.lookupUser = JSON.parse(JSON.stringify(temp))
    },
    setEmocApplication: (state, data) => {
      console.log('data: ', data)
      let setDefaultField = {
        E_MOC_Application_No__c: data.E_MOC_Application_No__c,
        Notification_Type__c: data.Notification_Type__c || 'CR',
        Functional_Location__c: data.Functional_Location__c,
        Functional_Location_Description__c: data.Functional_Location_Description__c,
        Plant_of_Main_Work_Center__c: data.Plant_of_Main_Work_Center__c,
        Main_Work_Center__c: data.Main_Work_Center__c,
        Requestor_ID__c: data.Requestor_ID__c,
        Requestor_Name__c: data.Requestor_Name__c,
        Requester__c: data.Requester__c,
        // Description__c: '',
        Department_Resp__c: data.Department_Resp__c,
        Indicator__c: data.Indicator__c,
        Notification__c: data.Notification__c,
        Integration_Status__c: data.Integration_Status__c,
        Integration_Message__c: data.Integration_Message__c,
        Order__c: data.Order__c,
        Notification_Status__c: data.Notification_Status__c,
        User_Status__c: data.User_Status__c,
        Priority__c: data.Priority__c,
        Main_Work_Center_Text__c: data.Main_Work_Center_Text__c,
        Name: data.Name,
        Employee_Id__c: data.Employee_Id__c,
        Notification_Closed_date__c: data.Notification_Closed_date__c
      }

      // console.log('setEmocApplication' + setDefaultField)
      // setDefaultField.Notification_Type__c = 'CR'
      state.emoc = Object.assign(state.emoc, setDefaultField)

      // state.emoc = setDefaultField
    },
    setRoundTimeout: (state, numOfTime) => {
      state.roundOfTimeout = numOfTime
    }
  },
  actions: {
    receiveLookupFncLocation: ({commit}, sendParam) => {
      // console.log('receiveLookupFncLocation', sendParam)
      createNotificationApi.getLookupFncLocation(result => {
        // console.log('result', result)
        commit('setLookupFncLocation', result)
      }, sendParam)
    },
    receiveLookupUser: ({commit}, {type, sendParam}) => {
      createNotificationApi.getLookupUser(result => {
        commit('setLookupUser', {type, data: result})
      }, sendParam)
    },
    receiveEmocApplication: ({commit}, sendParam) => {
      console.log('receiveEmocApplication: ', sendParam)
      createNotificationApi.getEmocApplication(result => {
        // console.log('result create emoc', result)
        commit('setEmocApplication', result)
      }, sendParam)
    },
    receiveNotification: ({dispatch, commit, state}, {sendParam, afterSet}) => {
      createNotificationApi.getNotification(result => {
        if ((result.Integration_Status__c === 'In Progress' || result.Integration_Status__c === '' || typeof result.Integration_Status__c === 'undefined') && state.roundOfTimeout < 6) {
          setTimeout(() => {
            dispatch('receiveNotification', {sendParam, afterSet})
            commit('setRoundTimeout', state.roundOfTimeout + 1)
          }, 5000)
        } else {
          commit('setEmocApplication', result)
          commit('setRoundTimeout', 0)
          afterSet()
        }
      }, sendParam)
    },
    submitNotification: ({commit}, paramSave) => {
      // console.log('submitNotification paramSave', paramSave)
      return new Promise((resolve, reject) => {
        createNotificationApi.submitNotification(result => {
          resolve(result)
        }, paramSave)
      })
    },
    putNotification: ({commit}, paramSave) => {
      // console.log('putNotification paramSave', paramSave)
      return new Promise((resolve, reject) => {
        createNotificationApi.putNotification(result => {
          resolve(result)
        }, paramSave)
      })
    },
    deleteNotification: ({commit}, paramSave) => {
      // console.log('putNotification paramSave', paramSave)
      return new Promise((resolve, reject) => {
        createNotificationApi.deleteNotification(result => {
          resolve(result)
        }, paramSave)
      })
    },
    sendMsg: ({ commit }, paramMsg) => {
      createNotificationApi.sendMsg(result => {
        if (result) {
          console.log('sendMsg successfully', result)
        }
      }, paramMsg)
    }
  }
})



